<template>
    <div class="vicky-loading-group" v-if="showLoading">
        <div class="mask"></div>
        <div class="loading-content">
            <div class="wrapper">
                <img class="loading-pic" src="data:image/gif;base64,">
                <p
                class="loading-text"
                v-show="text?'true':'false'"
                >{{text}}</p>
            </div>
        </div>
    </div>
</template>
<script>
export default{
  name: 'vicky-loading',
  props: {
    text: {
      type: String
    },
    showLoading: {
      type: Boolean,
      default: true
    }
  }
}
</script>
<style lang="stylus" scoped>
.vicky-loading-group
    .mask
        width 100%
        height 100%
        background-color rgba(0,0,0,0)
        position fixed
        left 0
        top 0
        z-index 100
    .loading-content
        width 120px
        height 120px
        background-color rgba(0,0,0,0.8)
        position fixed
        left 50%
        top 50%
        transform translate(-50%,-50%)
        border-radius 10px
        .wrapper
            display flex
            justify-content center
            align-items center
            flex-direction column
            width 100%
            height 100%
            .loading-pic
                width 35px
                height 35px
                margin-bottom 10px
            .loading-text
                width 100%
                text-align center
                padding 0 5px
                color #fff
                font-size 12px
                line-height 14px
                overflow hidden
                text-overflow ellipsis
                display -webkit-box
                -webkit-box-orient vertical
                -webkit-line-clamp 2
</style>



